.container-backEnd {
    max-width: 920px;
    margin: 0 auto;
    padding: 32px 0;
}
.card {
    border: 1px solid $gray-400;
    border-radius: 2px;
}
.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 16px;
}
.card-body {
    padding: 16px 32px 22px 16px;
}